import Splider from '../../components/Splider.vue'
export default {
	data(){
		return {
			
			imageList : [
				{
					img : "../../../static/轮播图/1.jpg"
				},
				{
					img : "../../../static/轮播图/2.jpg"
				},
				{
					img : "../../../static/轮播图/3.jpg"
				},
				{
					img : "../../../static/轮播图/4.jpg"
				},
				{
					img : "../../../static/轮播图/5.jpg"
				}
			],
			index : 0,
			newsList : []
		}
	},
	components:{
		Splider
	},
	methods:{
		toDetail(src){
			console.log(src);
			uni.navigateTo({
				url: "./detail?main=" + JSON.stringify(src)
			})
		},
		initNews(index){
			uni.request({
				url:"http://localhost:5000/keep/spider/news",
				method:"POST",
				data:{
					index : index
				},
				success: (res) => {
					this.newsList = res.data
				}
			})
		}
	},
	onShow(){
		this.initNews(this.index)
	}
}
